.inputbox {
  padding: 20px 0 18px;
  background: none;
  border: none;
  border-bottom: 2px solid #969696;
  height: 76px;
  line-height: 1.2;
  font-size: 30px;
  color: #fff;
  width: 100%;
  transition: all 0.3s;
  box-sizing: border-box;
}

.inputbox.placeholder {
  opacity: 1;
  color: #9c9c9c;
}

.inputbox:-moz-placeholder {
  opacity: 1;
  color: #9c9c9c;
}

.inputbox::-moz-placeholder {
  opacity: 1;
  color: #9c9c9c;
}

.inputbox:-ms-input-placeholder {
  opacity: 1;
  color: #9c9c9c;
}

.inputbox::-webkit-input-placeholder {
  opacity: 1;
  color: #9c9c9c;
}

.inputbox:focus {
  border-color: #9f00ff;
  box-shadow: 0 3px 0 #9f00ff;
}

.inputbox.error {
  color: #f26c4f;
}

.inputbox.error + label {
  color: #f26c4f;
}

.input_wrap {
  display: inline-block;
  width: 100%;
  position: relative;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
}

.input_wrap .inputbox {
  padding-right: 40px;
}

.input_wrap label {
  position: absolute;
  left: 0;
  top: 20px;
  color: #969696;
  line-height: 1.2;
  font-size: 30px;
  transition: all 0.3s;
}

.input_wrap .inputbox:focus + label, .input_wrap .inputbox.not_empty + label {
  top: 0;
  line-height: 20px;
  font-size: 18px;
  color: #969696;
}

.input_wrap:before, .input_wrap:after {
  content: '';
  position: absolute;
}

.input_wrap.success:after {
  right: 0;
  top: 20px;
  background-image: url("../images/sprite.png");
  background-position: -446px -182px;
  width: 37px;
  height: 37px;
}

.input_wrap.error:after {
  right: 0;
  top: 20px;
  background-image: url("../images/sprite.png");
  background-position: -446px -143px;
  width: 37px;
  height: 37px;
}

.input_wrap.error label {
  color: #f26c4f;
}

.select_wrap select {
