  top: 12px;
  width: 100px;
  text-align: center;
  font: bold 20px/26px Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;
  color: #ffffff;
  background: #66bc00;
  text-transform: uppercase;
  transform: rotate(-45deg);
}

.invite {
  margin: 0 -20px;
  background: #f1f1f1;
  padding: 0 20px;
  position: relative;
}

.invite_inn {
  padding: 33px 24px;
  position: relative;
}

.invite_close {
  position: absolute;
  right: 0;
  top: 20px;
  background-image: url("../images/sprite.png");
  background-position: -446px -273px;
  width: 35px;
  height: 35px;
}

.invite_text {
  display: inline-block;
  vertical-align: middle;
  width: 457px;
  text-align: right;
  font-size: 24px;
  line-height: 30px;
}

.invite_text p {
  margin: 0;
}

.invite_text .green {
  color: #66bc00;
}

.invite_button {
  display: inline-block;
  vertical-align: middle;
  width: 133px;
  height: 50px;
  color: #ffffff;
  background: #66bc00;
  border-radius: 25px;
  text-align: center;
  font-size: 24px;
  margin-left: 19px;
}

.footer_inn, .grid {
  zoom: 1;
}

.footer_inn:before, .grid:before, .footer_inn:after, .grid:after {
  content: " ";
  display: table;
}

.footer_inn:after, .grid:after {
  clear: both;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
  margin: 0 auto;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@media print {
  * {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a, a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  pre, blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr, img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  @page {
    margin: .5cm;
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
}
