.inputbox {
	padding: 20px 0 18px;
	background: none;
	border: none;
	border-bottom: 2px solid $grey3;
	height: 76px;
	line-height: 1.2;
	font-size: 30px;
	color: #fff;
	width: 100%;
	transition: all 0.3s;
	@include box;
	@include placeholder {
		opacity: 1;
		color: #9c9c9c; }
	&:focus {
		border-color: $brand;
		box-shadow: 0 3px 0 $brand; }
	&.error {
		color: $red;
		&+label {
			color: $red; } } }
.input_wrap {
	display: inline-block;
	width: 100%;
	position: relative;
	line-height: 0;
	font-size: 0;
	overflow: hidden;
	.inputbox {
		padding-right: 40px; }
	label {
		position: absolute;
		left: 0;
		top: 20px;
		color: $grey3;
		line-height: 1.2;
		font-size: 30px;
		transition: all 0.3s; }
	.inputbox:focus+label, .inputbox.not_empty+label {
		top: 0;
		line-height: 20px;
		font-size: 18px;
		color: $grey3; }
	&:before, &:after {
		content: '';
		position: absolute; }
	&.success {
		&:after {
			right: 0;
			top: 20px;
			@include sprite($correct); } }
	&.error {
		&:after {
			right: 0;
			top: 20px;
			@include sprite($error); }
		label {
			color: $red; } } }
.select_wrap {
	select {
		box-shadow: none;
		background-color: transparent;
		background-image: none;
		appearance: none;
		position: relative;
		z-index: 2; }
	&:before {
		@include sprite($select-arr);
		right: 7px;
		top: 33px; } }












select.inputbox {}


textarea.inputbox {
	height: auto; }

button {
	background: none;
	border: none; }
.button {
	display: inline-block;
	border: none;
	text-decoration: none;
	text-align: center;
	background: rgba(#fff, 0.2);
	color: #fff;
	padding: 0 50px 9px;
	height: 100px;
	line-height: 1;
	font-size: 36px;
	@include rad(50px);
	@include box;
	position: relative;
	&:after {
		content: '';
		display: inline-block;
		vertical-align: middle;
		margin-left: 9px;
		@include sprite($small-arrow);
		position: relative; } }

.form_list {
	margin: 0;
	list-style: none;
	font-size: 1px;
	line-height: 0;
	li {
		margin-bottom: 32px; } }


