@charset "UTF-8";

@import "var";
@import "sprite";

body {
	font: #{$base-fz}/#{$base-lh} $font;
	color: #fff;
	.bgf & {
		color: $grey4; } }
select, input, textarea, button {
	font: 99% $font; }
pre, code, kbd, samp {
	font-family: monospace, sans-serif; }
a {
	text-decoration: none;
	color: $link; }

h1,h2,h3,h4,h5,h6,b,strong {
	font-weight: bold; }
em,i {
	font-style: italic; }
h1,h2,h3,h4,h5,h6,p,blockquote,ul,ol,dl,address,table {
	margin-bottom: 1em; }
.menu {
	margin: 0;
	list-style: none; }
h6 {
	font-size: 100%; }
h5 {
	font-size: 115%; }
h4 {
	font-size: 125%; }
h3 {
	font-size: 150%; }
h2 {
	font-size: 175%; }
h1 {
	font-size: 200%; }
sub, sup {
	font-size: 75%;
	line-height: 0;
	position: relative; }
sup {
	top: -0.5em; }
sub {
	bottom: -0.25em; }

@import "form";


html {
	//height: 100%
	//height: 100vh
	min-width: $w;
	background: #000 url(../images/bg.jpg) 50% no-repeat;
	background-size: cover;
	&.bg2, &.bg3 {
		background-image: url(../images/bg2.jpg); }
	&.bgf {
		background: #ffffff; } }

body {
	//width: 100%
	//height: 100%
	//width: 100vw
 }	//height: 100vh

.wrapper {
	width: $w;
	@include box;
	position: relative;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	.bg2 &, .bgf & {
		justify-content: flex-start; } }
section {
	padding: 60px 40px;
	position: relative;
	&.camera {
		padding: 0; }
	&.checkout {
		padding-top: 199px; }
	&.home {
		padding: 128px 20px 20px; } }




%jus {
	list-style: none;
	text-align: justify;
	font-size: 1px;
	line-height: 0;
	zoom: 1;
	&:after {
		width: 100%;
		height: 0px;
		visibility: hidden;
		overflow: hidden;
		content: '';
		display: inline-block; }
	>* {
		display: inline-block;
		vertical-align: top;
		line-height: normal;
		font-size: $base-fz; }
	>.empty {
		margin: 0;
		padding: 0;
		height: 0;
		overflow: hidden;
		visibility: hidden; } }



.splash_logo {
	display: block;
	margin: 0 auto;
	width: 205px;
	height: 91px;
	span {
		float: left;
		text-indent: -999em;
		opacity: 0;
		transition: all 0.5s ease-out; }
	.logo {
		@include sprite($logo);
		transform:  translateX(-999px); }
	.dots {
		@include sprite($logo-dots);
		margin-left: 7px;
		margin-top: 16px;
		transform: translateY(-999px); }
	&.is-active {
		.logo {
			transform:  translateX(0);
			opacity: 1; }
		.dots {
			transform:  translateY(0);
			opacity: 1; } } }


.main_logo {
	display: block;
	margin: 0 auto;
	width: 205px;
	height: 91px;
	span {
		float: left;
		text-indent: -999em; }
	.logo {
		@include sprite($logo); }
	.dots {
		@include sprite($logo-dots);
		margin-left: 7px;
		margin-top: 16px; } }


.login_form {
	margin: 122px 20px 0 20px;
	padding: 0 60px 60px;
	border-bottom: 2px solid rgba(#fff,0.3);
	.inputbox {
		line-height: 50px;
		padding: 14px 40px 26px 76px;
		height: 91px; }
	.input_wrap {
		label {
			left: 76px;
			top: 14px;
			line-height: 50px; }
		&.success {
			&:after {
				top: 29px; } }
		&.error {
			&:after {
				top: 29px; } } }
	.input_user {
		&:before {
			@include sprite($username);
			left: 0;
			top: 12px; } }
	.input_pass {
		&:before {
			@include sprite($password);
			left: 0;
			top: 12px; } }
	.form_list {
		li {
			margin-bottom: 16px; }
		.buttons {
			margin-bottom: 0;
			margin-top: 60px; } }
	.button {
		width: 100%; } }
.form_links {
	margin: 48px 0 0;
	list-style: none;
	text-align: center;
	li+li {
		margin-top: 44px; } }
.register_form {
	margin-top: 64px;
	.jus {
		@extend %jus;
		.input_wrap {
			width: 303px; }
		.input_at {
			text-align: center;
			font-size: 30px;
			line-height: 1.2;
			padding: 20px 0 18px;
			color: $grey3; } }
	.buttons {
		margin-bottom: 0;
		margin-top: 60px;
		text-align: center; }
	.button {
		width: 510px; } }



.photo_wrapper {

	width: 100%;
	.photo_slide+.photo_slide {
		display: none; }
	img {
		display: block;
		margin: 0 auto;
		max-width: 100%;
		max-height: 100%;
		width: auto;
		height: auto; } }
.photo_header {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 88px;
	padding: 23px 40px;
	background: rgba(#000,0.8);
	@include box; }
.camera_switch {
	float: left;
	@include sprite($camera-switch);
	margin-right: 76px;
	cursor: pointer; }
.camera_flash {
	float: left;
	@include sprite($flash);
	cursor: pointer; }
.camera_close {
	float: right;
	@include sprite($close);
	cursor: pointer; }

.photo_thumbs {
	position: relative;
	padding-top: 45px;
	.slick-dots {
		position: absolute;
		left: 0;
		top: 20px;
		line-height: 0;
		font-size: 1px;
		text-align: center;
		width: 100%;
		li {
			display: inline-block;
			margin: 0 10px; }
		button {
			display: inline-block;
			width: 10px;
			height: 10px;
			@include rad();
			text-align: center;
			color: #ffffff;
			background: #ffffff;
			line-height: 10px;
			font-size: 1px; } } }
.photo_thumb {
	margin: 5px;
	text-align: center; }
.photo_footer {
	position: absolute;
	left: 0;
	bottom: 0;
	background: rgba(#000,0.8);
	width: 100%; }
.camera_tools {
	height: 203px;
	padding: 49px 21px 54px 40px;
	@include box;
	text-align: center;
	line-height: 0;
	font-size: 1px; }
.camera_photos {
	float: left;
	width: 100px;
	height: 100px;
	@include box;
	border: 2px solid #fff;
	@include rad();
	position: relative;
	@include sprite-ico($photos1);
	cursor: pointer; }
.camera_next {
	float: right;
	width: 100px;
	height: 100px;
	@include rad();
	background: rgba(#fff,0.2);
	position: relative;
	@include sprite-ico($next1);
	cursor: pointer; }
.camera_shot {
	display: inline-block;
	width: 100px;
	height: 100px;
	background: $brand;
	@include rad();
	position: relative;
	@include sprite-ico($camera1);
	cursor: pointer; }
.checkout_form_header {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 88px;
	background: rgba(#000,0.8);
	padding: 18px 20px 17px 40px;
	@include box;
	.back {
		float: left;
		@include sprite($back1); }
	.close {
		float: right;
		margin: 5px 0;
		@include sprite($close2); } }

.checkout_form_ico {
	width: 100px;
	height: 100px;
	@include box;
	border: 2px solid #fff;
	margin: 0 auto 20px;
	@include rad();
	position: relative;
	&.ico1 {
		@include sprite-ico($location1); }
	&.ico2 {
		@include sprite-ico($doller1); }
	&.ico3 {
		@include sprite-ico($questions1); } }
.checkout_form_title {
	text-align: center;
	font-size: 48px;
	line-height: 1.2;
	margin-bottom: 86px; }
.checkout_form {
	.buttons {
		text-align: center;
		margin-bottom: 0;
		margin-top: 60px; }
	.button {
		width: 350px;
		&:after {
			position: absolute;
			right: 38px;
			top: 50%;
			margin-top: -25px;
			@include sprite($next2); }
		&.done {
			&:after {
				display: none; } } }
	.price {
		margin-top: -15px;
		position: relative;
		overflow: visible;
		&:before {
			content: '$';
			font-size: 72px;
			line-height: 1;
			position: absolute;
			left: 0;
			top: -8px; }
		.inputbox {
			font-size: 72px;
			padding: 0 64px 36px;
			text-align: center;
			line-height: 1;
			height: 93px; } } }
.checkout_form_done {
	display: block;
	width: 238px;
	height: 238px;
	background: rgba(#000,0.6);
	border: 10px solid rgba($green,0.6);
	@include rad();
	margin: 0 auto;
	position: relative;
	text-indent: -999em;
	@include sprite-ico($done-confermation); }

.header {
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	padding: 58px 0 10px;
	background: $dblue;
	box-shadow: 0 4px 4px rgba(#000,0.4);
	z-index: 5; }
.header_search {
	width: $w;
	@include box;
	padding: 0 20px;
	margin: 0 auto;
	position: relative; }
.search_form {
	margin-right: 72px;
	position: relative;
	.inputbox {
		width: 100%;
		height: 60px;
		background: #ffffff;
		border: none;
		@include box;
		padding: 10px 40px 10px 140px;
		line-height: 40px;
		font-size: 30px;
		color: $grey4;
		@include rad(10px);
		&:focus+label, &.not_empty+label {
			left: 0;
			margin-left: 0; } }
	label {
		height: 60px;
		padding-left: 48px;
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: -67px;
		transition: all 0.3s;
		span {
			display: inline-block;
			vertical-align: top;
			margin-top: 12px;
			@include sprite($logo-small); }
		&:before {
			content: '';
			position: absolute;
			left: 10px;
			top: 50%;
			@include sprite($search);
			margin-top: -12px; } } }
.header_filter {
	position: absolute;
	right: 20px;
	top: 0;
	width: 50px;
	height: 60px;
	@include sprite-ico($filter); }


.footer {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	z-index: 5;
	button {
		width: 100px;
		height: 100px;
		background: $brand;
		@include rad();
		box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.4);
		position: relative; }
	.menu {
		float: left;
		@include sprite-ico($menu); }
	.photo {
		float: right;
		@include sprite-ico($camera1); } }
.footer_inn {
	width: $w;
	padding: 0 20px 20px;
	@include box;
	@extend %cf;
	margin: 0 auto; }




.grid {
	@extend %cf; }
.grid_item {
	float: left;
	width: 345px;
	margin-top: 20px; }
.item_link {
	color: $grey3;
	display: block;
	font-size: 24px;
	background: $grey1;
	position: relative;
	overflow: hidden; }
.item_img {
	display: block;
	img {
		display: block;
		max-width: 100%; } }
.item_foot {
	display: block;
	padding: 13px 20px; }
.grid_title {
	font-size: 30px;
	line-height: 36px;
	display: block;
	color: $grey4; }
.grid_price {
	color: $green; }
.grid_model {
	&:before {
		content: '|';
		color: $grey2;
		margin: 0 15px; } }
.item-label {
	position: absolute;
	left: -24px;
	top: 12px;
	width: 100px;
	text-align: center;
	font: bold 20px/26px $ar;
	color: #ffffff;
	background: $green;
	text-transform: uppercase;
	transform: rotate(-45deg); }


.invite {
	margin: 0 -20px;
	background: $grey1;
	padding: 0 20px;
	position: relative; }
.invite_inn {
	padding: 33px 24px;
	position: relative; }
.invite_close {
	position: absolute;
	right: 0;
	top: 20px;
	@include sprite($close3); }
.invite_text {
	display: inline-block;
	vertical-align: middle;
	width: 457px;
	text-align: right;
	font-size: 24px;
	line-height: 30px;
	p {
		margin: 0; }
	.green {
		color: $green; } }
.invite_button {
	display: inline-block;
	vertical-align: middle;
	width: 133px;
	height: 50px;
	color: #ffffff;
	background: $green;
	@include rad(25px);
	text-align: center;
	font-size: 24px;
	margin-left: 19px; }


































%cf {
	zoom: 1;
	&:before,&:after {
		content: " ";
		display: table; }
	&:after {
		clear: both; } }



@import "slick";






@media print {
	* {
		background: transparent!important;
		color: #000!important;
		box-shadow: none!important;
		text-shadow: none!important; }
	a,a:visited {
		text-decoration: underline; }
	a[href]:after {
		content: " (" attr(href) ")"; }
	abbr[title]:after {
		content: " (" attr(title) ")"; }
	pre,blockquote {
		border: 1px solid #999;
		page-break-inside: avoid; }
	thead {
		display: table-header-group; }
	tr,img {
		page-break-inside: avoid; }
	img {
		max-width: 100%!important; }
	@page {
		margin: .5cm; }
	p,h2,h3 {
		orphans: 3;
		widows: 3; }
	h2,h3 {
		page-break-after: avoid; } }
