@mixin tdn {
    text-decoration: none;
    &:hover {
        text-decoration: underline; } }

@mixin placeholder {
    &.placeholder {
        @content; }
    &:-moz-placeholder {
        @content; }
    &::-moz-placeholder {
        @content; }
    &:-ms-input-placeholder {
        @content; }
    &::-webkit-input-placeholder {
        @content; } }

@mixin iconbg($w, $h, $bgp) {
    &:before, &>.before {
        width: $w;
        height: $h;
        margin: -#{$h/2} 0 0 -#{$w/2};
        background-position: $bgp;
        left: 50%;
        top: 50%; } }

@mixin before($w, $h, $bgp) {
    &:before, &>.before {
        width: $w;
        height: $h;
        margin: -#{$h/2} 0 0 0;
        background-position: $bgp;
        left: 0;
        top: 50%; } }

@mixin before2($w, $h, $l, $t, $bgp) {
    &:before {
        width: $w;
        height: $h;
        background-position: $bgp;
        left: $l;
        top: $t; } }

@mixin gradient($c1, $c2) {
    background-color: $c1;
    $experimental-support-for-svg: true;
    @include background-image(linear-gradient(top,  $c1 0%,$c2 100%));
    .ielt9 & {
        @include filter-gradient($c1, $c2, vertical); } }

//asdfast font-family
@mixin font($alias, $name, $weight:normal, $style:normal) {
    @font-face {
        font-family: $alias;
        src: url($name + ".woff2") format("woff2"), url($name + ".woff") format("woff");
        font-weight: $weight;
        font-style: $style; } }


// Responsive utilities

//
// More easily include all the states for responsive-utilities.less.
// [converter] $parent hack
@mixin responsive-visibility($parent) {
    #{$parent} {
        display: block !important;
        table#{$parent} {
            display: table; }
        tr#{$parent} {
            display: table-row !important; }
        th#{$parent}, td#{$parent} {
            display: table-cell !important; } } }

// [converter] $parent hack
@mixin responsive-invisibility($parent) {
    #{$parent} {
        display: none !important; } }

@mixin clr {
    &:after {
        content: " ";
        display: table;
        clear: both; }
    *zoom: 1; }
@mixin box {
    box-sizing: border-box; }
@mixin rad($r: 50%) {
    border-radius: $r; }
@mixin jus {
    margin: 0;
    list-style: none;
    text-align: justify;
    text-justify: newspaper;
    line-height: 0;
    font-size: 1px;
    zoom: 1;
    &:after {
        width: 100%;
        height: 0px;
        visibility: hidden;
        overflow: hidden;
        content: '';
        display: inline-block; }
    >* {
        display: inline-block;
        vertical-align: top; }
    >.empty {
        margin: 0;
        padding: 0;
        height: 0;
        overflow: hidden;
        visibility: hidden; } }

//triangle
@mixin arr($width, $height, $bg, $direction) {
    width: 0px;
    height: 0px;
    background: none;
    border-style: solid;
    @if $direction == t {
        border-width: 0 $width / 2 + px $height + px $width / 2 + px;
        border-color: transparent transparent $bg transparent; }
    @if $direction == r {
        border-width: $height / 2 + px 0 $height / 2 + px $width + px;
        border-color: transparent transparent transparent $bg; }
    @if $direction == b {
        border-width: $height + px $width / 2 + px 0 $width / 2 + px;
        border-color: $bg transparent transparent transparent; }
    @if $direction == l {
        border-width: $height / 2 + px $width + px $height / 2 + px 0;
        border-color: transparent $bg transparent transparent; }
    @if $direction == tl {
        border-width: $height + px $width + px 0 0;
        border-color: $bg transparent transparent transparent; }
    @if $direction == tr {
        border-width: 0 $width + px $height + px 0;
        border-color: transparent $bg transparent transparent; }
    @if $direction == br {
        border-width: 0 0 $height + px $width + px;
        border-color: transparent transparent $bg transparent; }
    @if $direction == bl {
        border-width: $width + px 0 0 $height + px;
        border-color: transparent transparent transparent $bg; } }

// example
// +arr(500,50, #000, l)
// +arr(500,50, #000, bl)

@mixin iphone {
    @media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
        @content; } }
@mixin counter {
	list-style: none;
	counter-reset: point;
	li:before {
		content: counter(point);
		counter-increment: point 1; } }
